import Vue from 'vue';
import Router from 'vue-router';
const _import = require('./_import_' + process.env.NODE_ENV);
// in development env not use Lazy Loading,because Lazy Loading large page will cause webpack hot update too slow.so only in production use Lazy Loading

/* layout */
import Layout from '../views/layout/Layout';

/* login */
const Login = _import('login/index');

/* dashboard */
const dashboard = _import('dashboard/index');

/* error page */
const Err404 = _import('404');

/*project module*/
const ProjectDetail = _import('page/project/tabBar');
const ProjectList = _import('page/project/list');

/*sample module*/
const SampleLotList = _import('page/sample/sampleLotList');

/*task module*/
const TaskList = _import('page/task/list');

/*System Page User*/
const User = _import('page/system/user/index');
const Users = _import('page/system/user/users');
/*System Page Permission*/
const Permissions = _import('page/system/user/permissions');
/*System Page roles*/
const Roles = _import('page/system/user/roles');
/* demo page */
const SummaryBasic = _import('summary/basic');
const SummaryLayout = _import('summary/layout');
const SummaryNav = _import('summary/nav');
const SummaryNotification = _import('summary/notification');

const ComponentButtons = _import('components/buttons');
const ComponentDialogs = _import('components/dialogs');
const ComponentFileUploader = _import('components/fileuploader');
const ComponentPanel = _import('components/panel');
const ComponentTabs = _import('components/tabs');


const TableBasic = _import('table/basic');
const TableInlineEdit = _import('table/inlineedit');
const TableBatch = _import('table/batch');

const FormControl = _import('forms/controls');
const FormLayout = _import('forms/layout');

const Sample1 = _import('page/sample1');
const detection = _import('page/detection');
Vue.use(Router);

/**
 * icon : the icon show in the sidebar
 * hidden : if `hidden:true` will not show in the sidebar
 * redirect : if `redirect:noredirect` will not redirct in the levelbar
 * noDropdown : if `noDropdown:true` will not has submenu in the sidebar
 * meta : `{ role: ['admin'] }`  will control the page role
 **/
export const constantRouterMap = [
    {path: '/login',name:'login', component: Login, hidden: true},
    {path: '/404', component: Err404, hidden: true},
    {
        path: '/',
        component: Layout,
        redirect: '/dashboard',
        name: 'Home',
        hidden: true,
        children: [{path: 'dashboard', component: dashboard}]
    }
]

export default new Router({
    // mode: 'history', //后端支持可开
    scrollBehavior: () => ({y: 0}),
    routes: constantRouterMap
});

export const asyncRouterMap = [
    {
        path: '/project',
        component: Layout,
        redirect: 'noredirect',
        name: 'project',
        desc:'项目管理',
        icon: 'item-nor',
        iconBg: 'bg-blue',
        children: [
            {path: 'detail',component: ProjectDetail, name: 'projectDetail', icon: 'zonghe',desc:'新建项目'},
            {path: 'list/2',component: ProjectList,name:'notDeliveryProject', desc: '未交付项目', icon: 'zonghe'},
            {path: 'list/4', component: ProjectList, name: 'goingProject', desc:'进行中项目', icon: 'zonghe'},
            {path: 'list/5', component: ProjectList, name: 'completeProject',desc:'已完成项目', icon: 'zonghe'},
            {path: 'list/delivery/2', component: ProjectList, name: 'deliveryProduct',desc:'交付生产', icon: 'zonghe'}
        ]
    },
    {
        path: '/sample',
        component: Layout,
        redirect: 'noredirect',
        name:'sample',
        desc: '样本管理',
        icon: 'example',
        iconBg: 'bg-blue',
        children: [
            {path: 'receive/list', component: SampleLotList, name:'sampleReceive',desc: '样本接收', icon: 'zonghe'},
            {path: 'all/list', component: SampleLotList,name:'sampleLotQuery', desc: '样本批次查询', icon: 'zonghe'},
            {path: 'nav', component: SampleLotList,name:'sampleQuery', desc: '全局样本查询', icon: 'zonghe'},
        ]
    },
    {
        path: '/task',
        component: Layout,
        redirect: 'noredirect',
        name:'task',
        desc: '任务管理',
        icon: 'taskman',
        iconBg: 'bg-blue',
        children: [
            {path: 'list/dispatch/3', component: ProjectList, name:'taskDispatch',desc: '任务调度', icon: 'zonghe'},
            {path: 'list/2', component: TaskList,name:'hadAllotTask', desc: '已调度任务', icon: 'zonghe'},
            {path: 'list/3', component: TaskList,name:'goingTask', desc: '进行中任务', icon: 'zonghe'},
            {path: 'list/4', component: TaskList,name:'completeTask', desc: '已完成任务', icon: 'zonghe'},
            {path: 'nav', component: TaskList,name:'TaskList', desc: '人员工时', icon: 'zonghe'},
        ]
    },
    // {
    //     path: '/task',
    //     component: Layout,
    //     redirect: 'noredirect',
    //     name:'task',
    //     desc: '我的任务',
    //     icon: 'taskman',
    //     iconBg: 'bg-blue',
    //     children: [
    //     ]
    // },
    {
        path: '/system',
        component: Layout,
        redirect: 'noredirect',
        desc: '系统管理',
        name: 'system',
        icon: 'gaishu',
        iconBg: 'bg-green',
        children: [
            {path:'/userManagement',component: User, name:'userManagement',desc: '用户角色', icon: 'zonghe',
                children:[
                    {path: 'users', component: Users,name:'users', desc: '用户列表', icon: 'zonghe'},
                    {path: 'roles', component: Roles,name:'roles', desc: '角色列表', icon: 'zonghe'},
                    {path: 'permissions', component: Permissions,name:'permissions', desc: '权限列表', icon: 'zonghe'}
                ]
            }
        ]
    },
    {
        path: '/summary',
        component: Layout,
        redirect: 'noredirect',
        desc: '概述',
        icon: 'gaishu',
        iconBg: 'bg-blue',
        children: [
            {path: 'basic', component: SummaryBasic, desc: '基本元素', icon: 'zonghe'},
            {path: 'layout', component: SummaryLayout, desc: '排版', icon: 'zonghe'},
            {path: 'nav', component: SummaryNav, desc: '导航说明', icon: 'zonghe'},
            {path: 'notification', component: SummaryNotification, name: '消息通知', icon: 'zonghe'}
        ]
    },
    {
        path: '/components',
        component: Layout,
        redirect: 'noredirect',
        desc: '组件',
        icon: 'zujian',
        iconBg: 'bg-red',
        children: [
            {path: 'button', component: ComponentButtons, desc: '按钮', icon: 'zonghe'},
            {path: 'dialog', component: ComponentDialogs, desc: '对话框', icon: 'zonghe'},
            {path: 'tab', component: ComponentTabs, desc: 'Tab标签', icon: 'zonghe'},
            {path: 'panel', component: ComponentPanel, desc: '面板', icon: 'zonghe'},
            {path: 'fileuploader', component: ComponentFileUploader, desc: '文件上传', icon: 'zonghe'}
        ]
    },
    {
        path: '/form',
        component: Layout,
        redirect: 'noredirect',
        desc: '表单',
        icon: 'biaodan',
        iconBg: 'bg-yellow',

        children: [
            {path: 'control', component: FormControl, desc: '表单控件', icon: 'zonghe'},
            {path: 'layout', component: FormLayout, desc: '表单布局', icon: 'zonghe'}
        ]
    },

    {
        path: '/table',
        component: Layout,
        redirect: 'noredirect',
        desc: '表格',
        icon: 'iconbiaoge',
        iconBg: 'bg-green',

        children: [
            {path: 'index', component: TableBasic, desc: '基本表格'},
            {path: 'batch', component: TableBatch, desc: '表格批量处理'},
            {path: 'inline', component: TableInlineEdit, desc: '表格行内编辑'},
        ]
    }, {
        path: '/sample',
        component: Layout,
        redirect: 'noredirect',
        desc: '典型页面',
        icon: 'dianxinganli',
        iconBg: 'bg-purple',

        children: [
            {
                path: 'sample1',name:'sample1', component: Sample1, desc: 'Sample'
            },
            {
                path: 'detection', component: detection, desc: 'Detection'
            }
        ]
    },
    {path: '*', redirect: '/404', hidden: true}
];



