import {parseTime} from '../utils/index.js';

export default {
	labelToValue: function(libraryIndex) {
		var str = "";
		var bool;
        var Letters = libraryIndex;
        var i;
        var c;
        for( i = 0; i < Letters.length; i ++ )   {   //Letters.length() ->>>>取字符长度
            c = Letters.charAt( i );
            if (Letters.indexOf( c ) ==-1)   { //在"Letters"中找不到"c"   见下面的此函数的返回值
                bool= true;
            }else{
                bool= false;
			}
        }
        if (bool) {
            libraryIndex = parseInt(libraryIndex);
        }else{
            libraryIndex = libraryIndex.toString();
		}
        switch (libraryIndex) {
			case '91':
			    str = "P5--Index--DNA--P7";
			    break;
			case '92':
				str = "P5--DNA--Index--P7";
				break;
            case '93':
                str = "P5--Index1--DNA--Index2--P7";
                break;
		}
		return str;
	},
    formatDate(time) {
        if (time != undefined && time != null) {
            var date = new Date(time);
            return parseTime(date, '{y}-{m}-{d}');
        }
    }
}


